import Movie from "@/components/movie";
import { fetchGenre } from "@/lib/fetchData";
import type { GenreType } from "@/types/global";

export default async function Genre({
  params,
}: {
  params: Promise<GenreType>;
}) {
  const { id, name } = await params;
  const movies = await fetchGenre(id);

  return (
    <div>
      <h2 className="text-lg font-bold mb-4 pb-2 border-b">{name}</h2>
      <div className="flex gap-2 flex-wrap">
        {movies.map((movie) => {
          return (
            <Movie key={movie.id} movie={movie} currentGenreId={String(id)} />
          );
        })}
      </div>
    </div>
  );
}
